import React, { useEffect, useState, useMemo } from 'react';
import Papa from 'papaparse';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

// 1. Registra os componentes do Chart.js
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

// 2. Interface para os dados do CSV
interface PriceEntry {
  timestamp: string;
  produto_base: string;
  nome_completo_raspado: string;
  preco: number;
  imagem_url: string;
  loja: string;
  url: string;
}

// 3. Cores fixas para cada loja
const storeColors = {
  Kabum: {
    border: 'rgb(255, 100, 0)', // Laranja
    background: 'rgba(255, 100, 0, 0.5)',
  },
  Pichau: {
    border: 'rgb(53, 162, 235)', // Azul
    background: 'rgba(53, 162, 235, 0.5)',
  },
  Terabyte: {
    border: 'rgb(75, 192, 192)', // Verde
    background: 'rgba(75, 192, 192, 0.5)',
  },
};

export function ProductComparison() {
  // Estado para todos os dados do CSV
  const [allData, setAllData] = useState<PriceEntry[]>([]);
  // Estado para os nomes únicos de produtos (para o dropdown)
  const [productNames, setProductNames] = useState<string[]>([]);
  // Estado para o produto que o usuário selecionou
  const [selectedProduct, setSelectedProduct] = useState<string>('');

  // Efeito para carregar e processar o CSV na montagem
  useEffect(() => {
    fetch('/precos.csv')
      .then((response) => response.text())
      .then((csvText) => {
        Papa.parse<PriceEntry>(csvText, {
          header: true,
          dynamicTyping: true,
          complete: (results) => {
            const data = results.data;
            
            // Remove linhas vazias ou com preço nulo/inválido
            const validData = data.filter(row => row.produto_base && row.preco !== null && row.preco !== undefined);

            setAllData(validData);

            // Pega os nomes únicos de produtos para o filtro
            const uniqueProducts = [...new Set(validData.map((row) => row.produto_base))];
            setProductNames(uniqueProducts);

            // Define o primeiro produto como padrão
            if (uniqueProducts.length > 0) {
              setSelectedProduct(uniqueProducts[0]);
            }
          },
        });
      });
  }, []); // Roda apenas uma vez

  // Hook para transformar os dados brutos em dados de gráfico
  // Isso só roda quando 'allData' ou 'selectedProduct' mudam
  const chartData = useMemo(() => {
    if (!selectedProduct) {
      return { labels: [], datasets: [] };
    }

    // 1. Filtra os dados apenas para o produto selecionado
    const productData = allData.filter(
      (row) => row.produto_base === selectedProduct
    );

    // 2. Agrupa os dados por data (ex: '26/10/2025')
    const dataByDate = new Map<string, PriceEntry[]>();
    
    productData.forEach((row) => {
      const dateKey = new Date(row.timestamp).toLocaleDateString('pt-BR');
      if (!dataByDate.has(dateKey)) {
        dataByDate.set(dateKey, []);
      }
      dataByDate.get(dateKey)!.push(row);
    });

    // 3. Cria os 'labels' (datas únicas no eixo X)
    const labels = [...dataByDate.keys()];

    // 4. Cria os 'datasets' (um para cada loja)
    const stores = ['Kabum', 'Pichau', 'Terabyte'];
    
    const datasets = stores.map((store) => {
      // Para cada loja, cria um array de preços correspondente aos labels
      const prices = labels.map((date) => {
        const entriesForDate = dataByDate.get(date) || [];
        const storeEntry = entriesForDate.find((e) => e.loja === store);
        
        // Se a loja não tiver preço naquele dia, ou for 0, usa 'null'
        // 'null' cria uma falha na linha do gráfico, o que é correto
        return storeEntry && storeEntry.preco > 0 ? storeEntry.preco : null;
      });

      const colors = storeColors[store as keyof typeof storeColors] || storeColors.Kabum;

      return {
        label: store,
        data: prices,
        borderColor: colors.border,
        backgroundColor: colors.background,
        tension: 0.1, // Deixa a linha levemente curvada
      };
    });

    return { labels, datasets };
  }, [allData, selectedProduct]);

  // Opções do Gráfico
  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top' as const,
      },
      title: {
        display: true,
        text: `Histórico de Preços: ${selectedProduct}`,
      },
      tooltip: {
         callbacks: {
            label: function(context: any) {
                let label = context.dataset.label || '';
                if (label) {
                    label += ': ';
                }
                if (context.parsed.y !== null) {
                    label += new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(context.parsed.y);
                }
                return label;
            }
         }
      }
    },
    scales: {
      y: {
        ticks: {
          callback: (value: number | string) => `R$ ${value}`,
        },
      },
    },
  };

  return (
    <div style={{ padding: '20px', maxWidth: '1000px', margin: 'auto' }}>
      <h1>Monitor de Preços</h1>

      {/* Filtro Dropdown de Produtos */}
      <div style={{ marginBottom: '20px' }}>
        <label htmlFor="product-select" style={{ marginRight: '10px', fontWeight: 'bold' }}>
          Selecionar Produto:
        </label>
        <select
          id="product-select"
          value={selectedProduct}
          onChange={(e) => setSelectedProduct(e.target.value)}
          style={{ padding: '5px' }}
        >
          {productNames.map((name) => (
            <option key={name} value={name}>
              {name}
            </option>
          ))}
        </select>
      </div>

      {/* O Gráfico de Linha */}
      {chartData.labels.length > 0 ? (
        <Line options={options} data={chartData} />
      ) : (
        <p>Carregando dados ou nenhum dado encontrado...</p>
      )}
    </div>
  );
}